import { h, Component } from 'preact';
import { Router, route } from 'preact-router';
import { createHashHistory } from 'history';
import { observer } from 'mobx-preact';
import { IntlProvider } from 'preact-i18n';

import util from 'preact-util';

import PubSub, { topics } from '../lib/pubsub';

import appState from '../stores/appstate';
import adminStore from '../stores/admin';
import waypointStore from '../stores/waypoint';
import userStore from '../stores/user';
import dogStore from '../stores/dog';
import teamStore from '../stores/team';
import workoutStore from '../stores/workout';
import workoutPlanStore from '../stores/workoutPlan';
import trackStore from '../stores/track';
import stravaActivityStore from '../stores/stravaActivity';
import garminActivityDetailsStore from '../stores/garminActivityDetails';

import definitionNo from './languages/no';
import definitionDe from './languages/de';
import definitionEn from './languages/en';
import definitionFr from './languages/fr';

const countryMap = {
    de: definitionDe,
    en: definitionEn,
    fr: definitionFr,
    no: definitionNo,
    default: definitionNo,
};

import Progress from './progress';
import Message from './message';
import RefreshSpinner from './refresh';
import ReadyToRefresh from './refresh/ready';
import Header from './header';
import Footer from './footer';
import Login from './login';
import Register from './register';
import Forgotten from './forgotten';

// Code-splitting is automated for routes
import Home from '../routes/home';
import HomeFeed from '../routes/home/feed';
import HomeTodo from '../routes/home/todo';
import HomeAdmin from '../routes/home/admin';

import Users from '../routes/users';
import UsersEdit from '../routes/users/edit';
import UsersConnect from '../routes/users/connect';
import UsersNotifications from '../routes/users/notifications';

import Dogs from '../routes/dogs';
import DogsList from '../routes/dogs/list';
import DogsTodo from '../routes/dogs/todo';
import DogsFeed from '../routes/dogs/feed';
import DogsDetail from '../routes/dogs/detail';
import DogsEdit from '../routes/dogs/edit';

import Workouts from '../routes/workouts';
import WorkoutsList from '../routes/workouts/list';
import WorkoutsDetail from '../routes/workouts/detail';
import WorkoutsEdit from '../routes/workouts/edit';
import WorkoutsMonth from '../routes/workouts/month';
import WorkoutsWeek from '../routes/workouts/week';
import WorkoutsPlan from '../routes/workouts/plan';

import Tracks from '../routes/tracks';
import TracksDetail from '../routes/tracks/detail';
import TracksEdit from '../routes/tracks/edit';

import Teams from '../routes/teams';
import TeamsDetail from '../routes/teams/detail';
import TeamsEdit from '../routes/teams/edit';
import TeamsAddMember from '../routes/teams/addMember';

import Search from '../routes/search';
import Logout from '../routes/logout';

@observer
class App extends Component {
	constructor(props) {
        super(props);
		this.stores = {
            appState,
            adminStore,
            waypointStore,
            userStore,
            dogStore,
            teamStore,
            workoutStore,
            workoutPlanStore,
            trackStore,
            stravaActivityStore,
            garminActivityDetailsStore,
        };
        this.state = {
            jwtToken: util.getJwtToken(),
            refreshTime: new Date().getTime(),
        };
        this.subscriptions();
        const { darkmode } = appState;
        util.toggleDarkModeClasses(darkmode);
    }

    subscriptions() {
        PubSub.subscribe(topics.LOG_OUT, () => {
            this.setState({
                jwtToken: undefined,
            });
            util.removeUserEmail();
            util.removeJwtToken();
        });

        PubSub.subscribe(topics.JWT_TOKEN_CHANGED, async (token) => {
            this.setState({
                jwtToken: token,
            });
            route('/', true);
        });
    }

	/** Gets fired when the route changes.
	 *	@param {Object} event		"change" event from [preact-router](http://git.io/preact-router)
	 *	@param {string} event.url	The newly routed URL
	 */
	handleRoute = e => {
		this.setState({ currentUrl: e.url });
        appState.setPrevPath(e.previous);
        appState.setPath(e.url);
        if (!e.url.match(/skipScroll/) && this.appContainer) {
            this.appContainer.scrollTo({ top: 0 });
        }
	};

    async loadAll() {
        appState.getFingerprint();
        await userStore.getInfo();
    }

    touchStart = (e) => {
        this.setState({
            startY: e.touches[0].pageY,
        })
    }

    touchEnd = () => {
        this.setState({
            startY: undefined,
            readyToRefresh: false,
        })
    }

    touchMove = (e) => {
        const { startY, refreshing } = this.state;
        const refreshRange = 100;
        const readyTorefreshRange = 50;
        const y = e.touches[0].pageY;
        const { scrollTop } = this.appContainer;
        if (scrollTop === 0 && y > startY + refreshRange && !refreshing) {
            this.setState({
                refreshing: true,
                readyToRefresh: false,
            });
            this.refreshPage();
        } else if (scrollTop === 0 && y > startY + readyTorefreshRange && !refreshing) {
            this.setState({ readyToRefresh: true });
        }
    }

    doneRefreshing() {
        clearTimeout(this.refreshTimer);
        this.refreshTimer = setTimeout(() => this.setState({
            startY: undefined,
            refreshing: false,
            readyToRefresh: false,
        }), 1000);
        
    }

    setRefreshTime = () => {
        const refreshTime= new Date().getTime();
        this.setState({ refreshTime });
    }

    refreshPage() {
        const { currentUrl } = this.state;
        this.setRefreshTime(currentUrl);
        switch (currentUrl) {
            case '/':
            case '/todo':
            case '/todo/':
            case '/feed':
            case '/feed/':
            case '/admin/':
                return this.refreshFrontpage();
            case '/workouts':
            case '/workouts/':
                return this.refreshWorkouts();
            case '/dogs':
            case '/dogs/':
                return this.refreshDogs();
            case '/teams':
            case '/teams/':
                return this.refreshTeams();
            case '/tracks':
            case '/tracks/':
                return this.refreshTracks();
            default:
                console.log(this.currentUrl);
                return this.doneRefreshing();
        }        
    }

    async refreshFrontpage() {
        // const { currentTeam } = dogStore;
        // await userStore.getInfo();
        // await userStore.getFeed({});
        // await userStore.getWorkoutSummary({});
        // await dogStore.load({ query: { team: currentTeam }, addData: ['workoutSummary'] });
        // this.setState({ refreshTime: new Date().getTime() });
        this.doneRefreshing();
    }

    async refreshWorkouts() {
        // await workoutStore.load();
        // this.setState({ refreshTime: new Date().getTime() });
        this.doneRefreshing();
    }

    async refreshDogs() {
        // await dogStore.load();
        this.doneRefreshing();
    }

    async refreshTeams() {
        // await teamStore.load();
        this.doneRefreshing();
    }

    async refreshTracks() {
        // await trackStore.load();
        this.doneRefreshing();
    }

    componentDidMount() {
        const inputProps = util.collectPropsFromElement(window.document.body);
        const apiServer = inputProps.apiServer || `${window.location.protocol}//${window.location.host}`;
        util.setApiServer(apiServer);
        this.loadAll();
    }

	render() {
        const { refreshing, jwtToken, readyToRefresh, currentUrl, refreshTime } = this.state;
        const { user } = userStore;
        const { language: localLanguage = 'no', path } = appState;
        const { language = localLanguage } = user;
        const languageDef = countryMap[language || 'default'];
		if (!jwtToken) {
            return (
                <IntlProvider definition={languageDef}>
                    <div id='app'>
                        <Progress />
                        <Router history={createHashHistory()}>
                            <Login stores={this.stores} {...this.props} path="/" />
                            <Login stores={this.stores} {...this.props} default />
                            <Login stores={this.stores} {...this.props} path="/:email" />
                            <Login stores={this.stores} {...this.props} path="/link/login/:logintoken" />

                            <Register stores={this.stores} path="/register/" />
                            <Register stores={this.stores} path="/register/:email" />

                            <Forgotten stores={this.stores} path="/forgotten/" />
                            <Forgotten stores={this.stores} path="/forgotten/:email" />
                        </Router>
                    </div>
                </IntlProvider>
			);		
        }
		return (
            <IntlProvider definition={languageDef}>
                <div id='app'
                    style={`overflow-x: auto; overflow-y: auto; height: 100%; ${readyToRefresh ? 'padding-top: 50px;' : ''} ${refreshing ? 'padding-top: 70px;' : ''} transition: .3s; overscroll-behavior: contain;`}
                    onTouchstart={this.touchStart}
                    onTouchend={this.touchEnd}
                    onTouchmove={this.touchMove}
                    ref={c => this.appContainer = c}
                >
                    <Progress />
                    <Message />
                    {readyToRefresh && <ReadyToRefresh />}
                    {refreshing && <RefreshSpinner />}

                    <Header stores={this.stores} />

                    <Router onChange={this.handleRoute} history={createHashHistory()}>
                        <Home stores={this.stores} path="/" key={refreshTime} />
                        <HomeFeed stores={this.stores} path="/feed" key={refreshTime} />
                        <HomeFeed stores={this.stores} path="/feed/:month/:year" key={refreshTime} />
                        <HomeFeed stores={this.stores} path="/feed/:month/:year/skipScroll" key={refreshTime} />
                        <HomeTodo stores={this.stores} path="/todo" key={refreshTime} />
                        <HomeAdmin stores={this.stores} path="/admin" key={refreshTime} />

                        <Users stores={this.stores} path="/users/" user="me" key={refreshTime} />
                        <UsersEdit stores={this.stores} path="/users/edit" key={refreshTime} />
                        <UsersConnect stores={this.stores} path="/users/connect" key={refreshTime} />
                        <UsersNotifications stores={this.stores} path="/users/notifications" key={refreshTime} />
                        <Users stores={this.stores} path="/users/:user" key={refreshTime} />
                        <Users stores={this.stores} path="/link/verify/:verifyEmailToken" key={refreshTime} />

                        <Dogs stores={this.stores} path="/dogs/" key={refreshTime} />
                        <Dogs stores={this.stores} path="/dogs/calendar/:month/:year" key={refreshTime} />
                        <Dogs stores={this.stores} path="/dogs/calendar/:month/:year/skipScroll" key={refreshTime} />
                        <DogsList stores={this.stores} path="/dogs/list" key={refreshTime} />
                        <DogsFeed stores={this.stores} path="/dogs/feed" key={refreshTime} />
                        <DogsFeed stores={this.stores} path="/dogs/feed/:month/:year" key={refreshTime} />
                        <DogsFeed stores={this.stores} path="/dogs/feed/:month/:year/skipScroll" key={refreshTime} />
                        <DogsTodo stores={this.stores} path="/dogs/todo" key={refreshTime} />
                        <DogsDetail stores={this.stores} path="/dogs/:dog" key={refreshTime} />
                        <DogsEdit stores={this.stores} path="/dogs/edit/:dog" key={refreshTime} />
                        <DogsDetail stores={this.stores} path="/dogs/:dog/:month" key={refreshTime} />
                        <DogsDetail stores={this.stores} path="/dogs/:dog/:month/:year" key={refreshTime} />
                        <DogsDetail stores={this.stores} path="/dogs/:dog/:month/:year/skipScroll" key={refreshTime} />
                        <DogsDetail stores={this.stores} path="/dogs/:dog/history/:month" key={refreshTime} />
                        <DogsDetail stores={this.stores} path="/dogs/:dog/history/:month/:year" key={refreshTime} />
                        <DogsDetail stores={this.stores} path="/dogs/:dog/history/:month/:year/skipScroll" key={refreshTime} />

                        <Workouts stores={this.stores} path="/workouts/" key={refreshTime} />
                        <Workouts stores={this.stores} path="/workouts/:month/:year" key={refreshTime} />
                        <Workouts stores={this.stores} path="/workouts/:month/:year/skipScroll" key={refreshTime} />
                        <WorkoutsList stores={this.stores} path="/workouts/list" key={refreshTime} />
                        <WorkoutsMonth stores={this.stores} path="/workouts/month/:month" key={refreshTime} />
                        <WorkoutsMonth stores={this.stores} path="/workouts/month/:month/:year" key={refreshTime} />
                        <WorkoutsMonth stores={this.stores} path="/workouts/month/:month/:year/skipScroll" key={refreshTime} />
                        <WorkoutsWeek stores={this.stores} path="/workouts/week/:week" key={refreshTime} />
                        <WorkoutsWeek stores={this.stores} path="/workouts/week/:week/:year" key={refreshTime} />
                        <WorkoutsWeek stores={this.stores} path="/workouts/week/:week/:year/skipScroll" key={refreshTime} />
                        <WorkoutsPlan stores={this.stores} path="/workouts/plan" key={refreshTime} />
                        <WorkoutsPlan stores={this.stores} path="/workouts/plan/:year" key={refreshTime} />
                        <WorkoutsPlan stores={this.stores} path="/workouts/plan/:year/skipScroll" key={refreshTime} />
                        <WorkoutsDetail stores={this.stores} path="/workouts/:workout" key={refreshTime} />
                        <WorkoutsEdit stores={this.stores} path="/workouts/edit/:workout" key={refreshTime} />

                        <Tracks stores={this.stores} path="/tracks/" key={refreshTime} />
                        <TracksDetail stores={this.stores} path="/tracks/:track" key={refreshTime} />
                        <TracksEdit stores={this.stores} path="/tracks/edit/:track" key={refreshTime} />

                        <Teams stores={this.stores} path="/teams/" key={refreshTime} />
                        <TeamsDetail stores={this.stores} path="/teams/:team" key={refreshTime} />
                        <TeamsEdit stores={this.stores} path="/teams/edit/:team" key={refreshTime} />
                        <TeamsAddMember stores={this.stores} path="/teams/addmember/:team" key={refreshTime} />

                        <Search stores={this.stores} path="/search/:query" key={refreshTime} />
                        <Logout stores={this.stores} path="/logout/" key={refreshTime} />
                    </Router>
                    <Footer stores={this.stores} key={currentUrl} />
                </div>
            </IntlProvider>
		);
	}
}

export default App;
