import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer, withText } from 'preact-i18n';

import mu from '../../lib/musher-util';

import LineGraph from '../../components/lineGraph';

@withText((props) => ({
    week: <Text id='workout.graph.week' />,
    distance: <Text id='workout.graph.distance' />,
    ascend: <Text id='workout.graph.ascend' />,
    plan: <Text id='workout.graph.plan' />,
}))
@observer
class AdminGraph extends Component {
    render() {
        const { fieldsLeft = [], legendLeft = [], fieldsRight = [], legendRight = [] } = this.props;
        const { adminStore } = this.props.stores;
        const dataLeft = fieldsLeft.map(e => adminStore[e]);
        const dataRight = fieldsRight.map(e => adminStore[e]);

        const firstData = dataLeft[0] || [];
        const xTicks = [];
        const parts = firstData.length % 5;
        const partSize = Math.floor(firstData.length / parts);

        for (let i = 0, l = firstData.length; i < l; i += partSize) {
            xTicks.push(firstData[i].epoch);
        }

        return (
            <>
                <div class='w-100 mt-3 mb-4'>
                    {/* <xmp>graphWorkoutSummaryWeekDistanceCurrent: {JSON.stringify(graphWorkoutSummaryWeekDistanceCurrent, null, 4)}</xmp>
                    <xmp>graphWorkoutSummaryWeekDistancePrev: {JSON.stringify(graphWorkoutSummaryWeekDistancePrev, null, 4)}</xmp> */}
                    <LineGraph
                        stores={this.props.stores}
                        width={600}
                        height={200}
                        showXTicks={1}
                        xTicksVal={val => util.isoDate(val)}
                        xTicks={xTicks}
                        paddingTop={30}
                        lineFromXzero={true}
                        lineFromXmax={true}
                        // xTicksLink={'/workouts/week/'}

                        dataLeft={dataLeft}
                        legendLeft={legendLeft}
                        // yMaxLeft={30}
                        // yMinLeft={0}
                        showYTicksLeft={1}
                        // yTicksLeftPostfix={'km'}
                        // unitLeft={['km', 'km', 'km']}
                        avgWindowLeft={[0, 0, 0]}
                        smoothLeft={[1, 1, 1]}


                        dataRight={dataRight}
                        legendRight={legendRight}
                        // dataRight={[graphWorkoutSummaryWeekElevationCurrent, graphWorkoutSummaryWeekElevationPrev]}
                        // legendRight={[`${seasonStart} ${ascend}`, `${seasonStart - 1} ${ascend}`]}
                        showYTicksRight={dataRight.length > 0 ? 1 : 0}
                        // yTicksRightPostfix={'m'}
                        // unitRight={['m', 'm']}
                        avgWindowRight={[0, 0]}
                        smoothRight={[1, 1]}

                        // marker={week}
                    />
                </div>

            </>
        );
    }
}

export default AdminGraph;
