import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import linkState from 'linkstate';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(false);
const MARGIN_BOTTOM = mu.marginBottom();

import Error from '../../components/error';
import Message from '../../components/message';
import InviteFriend from '../../components/user/invite';
import User from '../../components/user';

function getIcon(type) {
	switch (type) {
		case 'dogHistory':
			return 'fas fa-rss';
		case 'dogStatus':
			return 'fas fa-dog';
		case 'dogSpecialNeeds':
			return 'fas fa-dog';
		case 'dogImage':
			return 'fas fa-image';
		case 'dog':
			return 'fas fa-dog';
		case 'dogChanged':
			return 'fas fa-dog';
		case 'track':
			return 'fas fa-map-marked-alt';
		case 'trackChanged':
			return 'fas fa-map-marked-alt';
		case 'workoutPlan':
			return 'fas fa-calendar-alt';
		case 'workout':
			return 'fas fa-running';
		case 'workoutChanged':
			return 'fas fa-running';
		case 'teamMember':
			return 'fas fa-user';
		case 'team':
			return 'fas fa-users';
		default:
			return 'fas fa-rss';
	}
}

@observer
class UserNotifications extends Component {
	constructor(props) {
        super(props);
        this.state = {
            time: Date.now(),
			count: 10
        };
    }
    async loadAll() {
        await this.loadNotifications();
        await this.markNotificationsAsRead();
    }

    async loadNotifications() {
        const { userStore } = this.props.stores;
        await userStore.getNotifications();
    }

    async markNotificationsAsRead() {
        const { userStore } = this.props.stores;
        const { notifications } = userStore;
        const ids = notifications.map(e => e.id);
        await userStore.markNotificationsAsRead({ ids });
    }

	search = async () => {
        const { userStore } = this.props.stores;
        const { searchText } = this.state;
        await userStore.searchUsers({ search: searchText });
    }

    toggleDate = (e) => {
        e.stopPropagation();
        e.preventDefault();
        const { showDate } = this.state;
        this.setState({ showDate: !showDate });
    }

	componentDidMount() {
        this.loadAll();
	}

	render() {
        const { showDate } = this.state;
		const { userStore } = this.props.stores;
		const { notifications } = userStore;
		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
                <div class='row'>
                    {notifications && notifications.map(notification => {
                        const { addedBy: user  = {} } = notification;
                        const displayNameShort = user.firstname ? `${util.ucfirst(user.firstname, true)}${util.ucfirst(user.lastname, true)}` : `${util.ucfirst(user.email || '', true)}`;
                        const displayName = user.firstname ? `${user.firstname} ${user.lastname}` : `${user.email}`;
                        const dogIds = [];
                        const dogs = [];
                        if (notification.grouped) {
                            notification.grouped.forEach((e) => {
                                if (e.dog && e.dog.id && dogIds.indexOf(e.dog.id) === -1) {
                                    dogs.push(e.dog);
                                    dogIds.push(e.dog.id);
                                }
                            });
                        }
                        const hasMultiElements = dogs.length > 0;
                        return (
                            <div class='col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 d-flex mb-3 position-relative' style={`opacity: ${notification.read ? 0.6: 1};`}>
                                <div class={`text-center border rounded-circle imageRoundedSmall`} style={`padding-top: 7px; ${user.image ? `background-image: url("${user.image.s3SmallLink}");` : ''}`}>
                                   {!user.image && <span class='font-weight-lighter' style='font-size: 15px;'>{displayNameShort}</span>}
                                </div>
                                <div class='flex-grow-1 pl-3 pr-2'>
                                    <span class='float-right ml-2'>
                                        {notification.type && <span class='mr-2'><i class={getIcon(notification.type)} /></span>}
                                    </span>
                                    {!hasMultiElements ? <a href={notification.url} class='stretched-link'>
                                        {notification.message}{notification.title ? `: ${notification.title}` : ''}
                                    </a> : <>
                                        {notification.message}
                                    </>}
                                    <br />
                                    <small class='text-muted'>
                                        {notification.date && <span onClick={this.toggleDate} class='mr-2'>
                                            {showDate ? util.formatDate(notification.date, { locale: 'no-NB' }) : util.formatDistance(notification.date, new Date(), { locale: 'no-NB' })}
                                        </span>}
                                        <i class='far fa-user' /> {displayName}
                                        <br />
                                        {dogs && dogs.map(d => <span class='mr-2'>
                                            <a href={`/dogs/${d.id}`}><i class='fas fa-dog' /> {d.name}</a>
                                        </span>)}
                                        <br />
                                    </small>
                                </div>
                            </div>
                        );
                    })}
				</div>
			</div>
		);
	}
}

export default UserNotifications;
